<template>
  <div class="expert-team-detail">
    <div class="container">
      <div class="section-header text-center">
        <h2>{{ teamTitle }}</h2>
        <p class="section-subtitle">{{ teamDesc }}</p>
      </div>
      <div class="row mt-5">
        <div v-for="expert in experts" :key="expert.name" class="col-md-4 col-sm-6 mb-4">
          <div class="expert-detail-card text-center">
            <img v-if="expert.avatar" :src="expert.avatar" class="expert-avatar-img mb-3" alt="专家头像" />
            <div v-else class="avatar-placeholder mb-3"><i class="fas fa-user"></i></div>
            <h4>{{ expert.name }}</h4>
            <p class="expert-title">{{ expert.title }}</p>
            <p class="expert-desc">{{ expert.desc }}</p>
          </div>
        </div>
      </div>
      <div class="text-center mt-4">
        <button class="btn btn-outline-primary" @click="$router.back()">返回</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ExpertTeamDetail',
  data() {
    return {
      experts: [],
      teamTitle: '',
      teamDesc: ''
    }
  },
  created() {
    const teamType = this.$route.params.teamType;
    // 这里可以根据实际需求从后端或本地获取数据
    const teamMap = {
      mingli: {
        title: '命理顾问团队',
        desc: '私人命理顾问团队，精通多年命理实践经验，精通八字、紫微斗数',
        experts: [
          { name: '王大师', title: '首席命理师', desc: '20年命理实战，擅长八字分析', avatar: '' },
          { name: '李老师', title: '命理顾问', desc: '紫微斗数专家，客户好评如潮', avatar: '' }
        ]
      },
      ai: {
        title: '数据智能专家组',
        desc: '智能算法架构师团队，深度学习技术赋能传统命理研究',
        experts: [
          { name: '张博士', title: 'AI算法专家', desc: '人工智能与命理结合的先行者', avatar: '' },
          { name: '陈工', title: '数据科学家', desc: '大数据建模与分析', avatar: '' }
        ]
      },
      medicine: {
        title: '中医养生专家组',
        desc: '体质调理专家，融合中医脉诊与生物数据分析技术',
        experts: [
          { name: '赵医生', title: '中医师', desc: '中医体质辨识与调理', avatar: '' },
          { name: '孙教授', title: '养生顾问', desc: '健康管理与养生方案设计', avatar: '' }
        ]
      },
      psychology: {
        title: '心理科学专家组',
        desc: '行为心理研究员，基于大数据的心理特征建模与干预方案设计',
        experts: [
          { name: '刘博士', title: '心理学家', desc: '心理特征建模与干预', avatar: '' },
          { name: '王老师', title: '心理咨询师', desc: '多年心理咨询经验', avatar: '' }
        ]
      }
    };
    if (teamMap[teamType]) {
      this.teamTitle = teamMap[teamType].title;
      this.teamDesc = teamMap[teamType].desc;
      this.experts = teamMap[teamType].experts;
    }
  }
}
</script>

<style scoped>
.expert-team-detail {
  padding: 40px 0;
}
.section-header h2 {
  font-size: 28px;
  font-weight: 700;
}
.expert-detail-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.06);
  padding: 30px 15px;
  min-height: 260px;
}
.expert-avatar-img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #eee;
}
.avatar-placeholder {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  color: #bbb;
  margin: 0 auto;
}
</style>


